"use client";

import React, { useState } from 'react';
import Swal from 'sweetalert2';
import "./ContactForm.css";

const Contact = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    msg: ''
  });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formBody = new FormData();
    formBody.append('name', formData.name);
    formBody.append('email', formData.email);
    formBody.append('subject', formData.subject);
    formBody.append('msg', formData.msg);
    formBody.append('_captcha', 'false'); 

    try {
      const response = await fetch(
        'https://formsubmit.co/ajax/hajji.youssef.contact@gmail.com',
        {
          method: 'POST',
          body: formBody,
          headers: { 'Accept': 'application/json' }
        }
      );

      const result = await response.json();
      if (result.success) {
        Swal.fire({
          title: 'Message Sent!',
          text: 'Your message was sent successfully.',
          icon: 'success'
        });
        setFormData({ name: '', email: '', subject: '', msg: '' }); 
      } else {
        Swal.fire({
          title: 'Oops!',
          text: 'Something went wrong. Please try again.',
          icon: 'error'
        });
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: 'Failed to send message.',
        icon: 'error'
      });
    }
  };

  return (
    <div className="form-container">
        <h2 className='services-main-title'>Ready To Build Your  <span className='orange'>Dream Space</span></h2>
        <div className="contact-section">
            <img src="/ctc-img.png" alt="" />
            <div className="form-content">
                <h3>Get In Touch With Us</h3>
                <form onSubmit={handleSubmit}>
        <div className="input-container">
        <input className='first-inp' type="text" name='name' placeholder="Full Name" value={formData.name} onChange={handleChange} />
        <input type="email" name='email' placeholder="Email" value={formData.email} onChange={handleChange} />
        </div>
        <div className="input-container">
        <input required className='first-inp' type="text" name='subject' placeholder="Subject" value={formData.subject} onChange={handleChange} />
        <input required type="number" name='number' placeholder='+212 123456789' />
        </div>
        <input required type="hidden" name="_captcha" value="false"/>
        <input required type="hidden" name="_template" value="table"/>
        <textarea required name='msg' placeholder="Message" value={formData.msg} onChange={handleChange}></textarea>
        <button type='submit' className="btn-53 btn2">
            <div className="original">Submit</div>
            <div className="letters">
              {"Submit".split("").map((char, i) => <span key={i}>{char}</span>)}
            </div>
          </button>
        {/* <div className="form-control">
            <input type="value" required/>
            <label>
            <span style={{ transitionDelay: '0ms' }}>U</span>
            <span style={{ transitionDelay: '50ms' }}>s</span>
            <span style={{ transitionDelay: '100ms' }}>e</span>
            <span style={{ transitionDelay: '150ms' }}>r</span>
            <span style={{ transitionDelay: '200ms' }}>n</span>
            <span style={{ transitionDelay: '250ms' }}>a</span><span style={{ transitionDelay: '300ms' }}>m</span>
<span style={{ transitionDelay: '350ms' }}>e</span>

            </label>
</div> */}
      </form>
            </div>
        </div>
    </div>
  );
};

export default Contact;
